<template>
<div id="box">
  <wu></wu>
	<div id="wu-1">
    <input type="text" v-model="txt"/>
    <input type="button" value="提交" @click="faA()"/>
  </div>
  <!-- 历史记录 -->
  <div v-show="bSys">
    <p>搜索历史</p>
    <div v-for="(item,index) in list" style="border-bottom:1px solid #999; line-height:1rem ">
     <p style=" height: 0.8rem"> {{ item.name}}</p>
     <p style="font-size:0.2rem"> {{item.name1}}</p>
      </div>
    <p style="text-align:center" @click="faB()">全部清除</p>
  </div>
  <!-- 提交显示内容 -->
  <div v-for="item in arr"style='border-bottom:1px solid'@click='faC(item)'>
    <p style="color:red" >{{item.name}}</p>
    <p style="font-size:0.2rem">{{item.address}}</p>
  </div>
</div>
</template>
<script>
	import wu from '../zhuye/toubiao'
export default {
components:{
		wu,
		// hui
	},
  name: 'box',
  data () {
    return {
       txt:'',
       list:[],
       arr:[],
       txt1:'el-icon-arrow-left',
       bSys:true
    }
  },
  methods:{
    faA(){
      if(this.txt!==''){
        this.bSys=!this.bSys
        this.axios.get('http://elm.cangdu.org/v1/pois?type=search&city_id='+this.$route.query.id.id+'&keyword='+this.txt).then((e)=>{
            this.arr=e.data
            console.log(this.arr)
        })
     
      }
    },
    faB(){
      localStorage.a=''
      this.list=[]
    },
    // 点击提交显示内容方法  
    // 往本地储存保存  然后方法跳转页面
    faC(e){
     var obj ={name:e.name,name1:e.address}
     this.list.unshift(obj)
      localStorage.a=JSON.stringify(this.list)
      this.$router.push({name:'gaa'})
    }
  },
  // 组件加载完成接受本地储存数据  显示历史纪录
  mounted(){
    if(localStorage.a){
      this.list=JSON.parse(localStorage.a)
    }
   
  },
 beforeMount(){
    localStorage.biao=this.txt1
    localStorage.zhu=this.$route.query.id.name
    },
}
</script>


<style>

#wu-1{
 
  width: 100%;
  height: 2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
#wu-1>input{
  width: 90%;
  height: 1rem;
  margin-top:0.2rem; 
  border:1px solid;
}
#wu-1>input:nth-of-type(2){
  background: blue;
  color: #fff;
  font-size: 0.4rem;
  border: 0px;
}
</style>


